import React, {useEffect, useState} from 'react'
import s2 from '../../s1-main/App.module.css'
import s from './HW15.module.css'
import axios, {AxiosError} from 'axios'
import SuperPagination from './common/c9-SuperPagination/SuperPagination'
import {useSearchParams} from 'react-router-dom'
import SuperSort from './common/c10-SuperSort/SuperSort'

/*
* 1 -  SuperPagination
* 2 -  SuperSort
* 3 -  pureChange 
* 3 -  sendQuery, onChangePagination, onChangeSort  HW15
* 4 -      
* 5 -  HW15  HW5/pages/JuniorPlus
* */

type TechType = {
    id: number
    tech: string
    developer: string
}

type ParamsType = {
    sort: string
    page: string
    count: string
}

type GetTechsResponse = { techs: TechType[]; totalCount: number };

export const getTechs = async (
    params: ParamsType
): Promise<GetTechsResponse> => {
    try {
        const res = await axios.get<GetTechsResponse>(
            'https://samurai.it-incubator.io/api/3.0/homework/test3',
            { params }
        );
        return res.data;
    } catch (e) {
        const err = e as AxiosError<{ errorText?: string }>;
        alert(err.response?.data?.errorText ?? err.message);
        throw e;
    }
};


const HW15 = () => {
    const [sort, setSort] = useState('')
    const [page, setPage] = useState(1)
    const [count, setCount] = useState(4)
    const [idLoading, setLoading] = useState(false)
    const [totalCount, setTotalCount] = useState(100)
    const [searchParams, setSearchParams] = useSearchParams()
    const [techs, setTechs] = useState<TechType[]>([])

    const sendQuery = (params: ParamsType) => {
        setLoading(true)
        getTechs(params)
            .then((res) => {
                //  
                setTotalCount(res.totalCount)
                //   
                setTechs(res.techs)
                //
            })
            .finally(() => {
                setLoading(false)
            })
    }

    const onChangePagination = (newPage: string, newCount: string) => {
        //  
        setPage(+newPage)
        setCount(+newCount)
        // setPage(
        // setCount(

        // sendQuery(
        // setSearchParams(
        sendQuery({page: newPage, count: newCount, sort})
        setSearchParams({ page: newPage, count: newCount, sort })

        //
    }

    const onChangeSort = (newSort: string) => {
        //  
        console.log(newSort)
        // setSort(
        setSort(newSort)
        // setPage(1) //     1 
        setPage(1)
        // sendQuery(
        // setSearchParams(
        sendQuery({ page: `${page}`, count: `${count}`, sort: newSort })
        //
        setSearchParams({ page: `${page}`, count: `${count}`, sort: newSort })
    }

    useEffect(() => {
        const params = Object.fromEntries(searchParams)
        sendQuery({page: params.page, count: params.count, sort})
        setPage(+params.page || 1)
        setCount(+params.count || 4)
    }, [])

    const mappedTechs = techs.map(t => (
        <div key={t.id} className={s.row}>
            <div id={'hw15-tech-' + t.id} className={s.tech}>
                {t.tech}
            </div>

            <div id={'hw15-developer-' + t.id} className={s.developer}>
                {t.developer}
            </div>
        </div>
    ))

    return (
        <div id={'hw15'}>
            <div className={s2.hwTitle}>Homework #15</div>

            <div className={s2.hw}>
                {idLoading && <div id={'hw15-loading'} className={s.loading}></div>}

                <SuperPagination
                    page={page}
                    itemsCountForPage={count}
                    totalCount={totalCount}
                    onChange={onChangePagination}
                />

                <div className={s.rowHeader}>
                    <div className={s.techHeader}>
                        tech
                        <SuperSort sort={sort} value={'tech'} onChange={onChangeSort}/>
                    </div>

                    <div className={s.developerHeader}>
                        developer
                        <SuperSort sort={sort} value={'developer'} onChange={onChangeSort}/>
                    </div>
                </div>

                {mappedTechs}
            </div>
        </div>
    )
}

export default HW15
